.searchWrapper{
    border: 1px solid rgba(51, 51, 51, 0.2);
    border-radius: 6px;
    height: 37px;
    padding: 0 6px;
    color: #333333;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    input{
        height: 20px;
        width: 90%;
        border: none;
        outline:none;
    }

    img{
        cursor: pointer;
    }
}