.title{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.4509803922);
    padding: 0 30px;
    height: 85px;
    flex-direction: row;
    flex-wrap: wrap;
}

@media (max-width: 734px){
    .title{
        justify-content: center;
        height: 130px;
    }
}
